// app/teams/[slug]/page.tsx
"use client";

import { notFound } from "next/navigation";
import Image from "next/image";
import { Trophy } from "lucide-react";
import { registeredTeams } from "./page";

export default function TeamPage({ params }: { params: { slug: string } }) {
  const team = registeredTeams.find((t) => t.slug === params.slug);
  console.log(params);
  if (!team) notFound();

  return (
    <div className="min-h-screen bg-background pt-20">
      {/* Hero */}
      <div className="bg-gradient-to-b from-[#0A1E3C] to-background py-20">
        <div className="max-w-7xl mx-auto px-4 text-center">
          <div className="w-48 h-48 mx-auto mb-8 relative">
            <Image
              src={team.logo || "/placeholder.svg"}
              alt={team.name}
              fill
              className="object-contain drop-shadow-2xl"
            />
          </div>
          <h1 className="text-6xl font-black text-white mb-4">{team.name}</h1>
        </div>
      </div>

      {/* More details: squad, fixtures, history, etc. */}
      <div className="py-20">
        <div className="max-w-7xl mx-auto px-4 grid md:grid-cols-3 gap-8">
          <div className="bg-card rounded-2xl p-8 text-center">
            <Trophy className="w-12 h-12 text-primary mx-auto mb-4" />
            <p className="text-4xl font-black">{team.titles}</p>
            <p className="text-muted-foreground">League Titles</p>
          </div>
          {/* Add more stats */}
        </div>
      </div>
    </div>
  );
}
